import React, { useEffect, useRef } from 'react';

import aboutImg from '../assets/aboutImg.png';
import reactLogo from '../assets/reactLogo.svg';
import gsap from 'gsap';

function About() {
  let text = useRef(null);
  useEffect(() => {
    gsap.from(text, { y: 50, opacity: 0, duration: 1 });
  });
  return (
    <div className="about">
      {/* <Sketch /> */}
      <h1 className="aboutTitle">About</h1>
      <h2
        className="aboutText"
        ref={(txt) => {
          text = txt;
        }}
      >
        Iwan Traeger-Payne is a developer from Woodstock, NY. in May 2020, Iwan
        graduated from the New Media program at Purchase College where he took a
        focus on creative programming. There he studied a variety of web, game,
        and physical computing technologies. He created projects that explored
        topics including AI, procedural generation, and particle systems. Iwan’s
        senior thesis “Media, Math, and Murmurations” explores how emergence,
        collective animal behavior, and virtual ecosystems can be applied to
        enhance generative works of art. Iwan continues to expand his expertise
        in web development and object orienting programming, collaborating in
        multiple technical and creative projects.
      </h2>
      <div className="reactCredit">
        <p className="creditText">site made with React</p>
        <img className="creditLogo" src={reactLogo} alt="react logo"></img>
      </div>

      <img
        className="aboutImage"
        src={aboutImg}
        alt="Iwan sitting on a roof"
      ></img>
    </div>
  );
}

export default About;
